/* ---------------trolly----------------- */
.main-container {
  width: 100%;
}

.tab-selection-container {
  border-bottom: 0.1px solid #c4c4c4;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: relative;
}

.tab-selection-container::before {
  content: '';
  display: block;
  width: 14rem;
  height: 0.6rem;
  background-color: #f8eddb;
  position: absolute;
  bottom: 0;
  border-radius: 0.5rem 0.5rem 0 0;
}

.tab-selection-container::after {
  content: '';
  display: block;
  width: 12rem;
  height: 1rem;
  background-color: #c4c4c4;
  position: absolute;
  -webkit-clip-path: polygon(0 0, 90% 0, 100% 100%, 0% 100%);
  clip-path: polygon(0 0, 90% 0, 100% 100%, 0% 100%);
}

.trolly-tab-selection {
  position: relative;
  width: 10rem;
  height: 10rem;
  display: inline-block;
  margin: 0 5rem 2rem 0;
  background-color: #fab84c;
  padding: 1rem;
  border-radius: 50%;
  overflow: none;
}

.lunch-tab-selection {
  width: 10rem;
  height: 10rem;
  display: inline-block;
  padding: 1rem;
  margin: 0 0 2rem 0;
  background-color: #fab84c;
  border-radius: 50%;
  overflow: none;
}
.trolly-img {
  width: 100%;
}

