.switch{
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;

}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.toggle-slider{
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    background-color: #ccc;
    transition: 0.4s;
    border-radius: 100px;
}

.toggle-slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left:4px;
    bottom: 4px;
    background-color: white;
    transition: 0.4s;
    border-radius: 100px;
}
input:checked + .toggle-slider{
    background-color: #D72525;
}
input:checked + .toggle-slider:before{
    -webkit-transform: translateX(26px);
            transform: translateX(26px);
}

